import React from 'react';

import { Navbar, Nav, NavItem, NavDropdown, MenuItem, Container, Row, Col } from 'react-bootstrap';
import {BrowserRouter,Route,Switch} from 'react-router-dom';
import Historia from './components/historia';
import Soittaminen from './components/soittaminen';
import Kuuluisuudet from './components/kuuluisuudet';
import Palaute from './components/palaute';
import Title from './components/title';
import MainNav from './components/MainNav';
import Greet from './components/greet';
import Footer from './components/footer';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

class App extends React.Component {


	render(){
		return (
      <BrowserRouter>
			<div className='App'>

      <Container>
        <Row>
          <Col>

            <Title />
            <MainNav />
            <div class="content">
              <Switch>
								<Route exact path="/react-gh-pages" component={Greet}/>
                <Route exact path="/" component={Greet}/>
                <Route path="/historia" component={Historia}/>
                <Route path="/soittaminen" component={Soittaminen}/>
                <Route path="/kuuluisuudet" component={Kuuluisuudet}/>
                <Route path="/palaute" component={Palaute}/>
              </Switch>
            </div>
            <Footer />

            </Col>
          </Row>
        </Container>

			</div>
      </BrowserRouter>
		);
	}
}

export default App;
