import React from "react";
import '../App.css';

class Historia extends React.Component {

  	render(){
  		return(
      <div className="Historia">
        <h1>Piano ja sen historia</h1>
        <p>
        Pianon kehitys sai alkunsa dulcimer-tyyppisistä soittimista, joihin keskiajalta lähtien yritettiin lukuisia kertoja liittää koskettimisto ja koneisto. Tämän kehityksen tuloksena eriytyivät ensin klavikordi ja cembalo.
        </p>
        <p>
        Modernin pianon kehittäjänä ja jopa keksijänä pidetään italialaista Bartolomeo Cristoforia (1655–1731). Kolme hänen 1720-luvulla rakentamaansa soitinta on säilynyt, ja niiden koneiston toimintaperiaate on sama kuin modernissa konserttiflyygelissä. Cristofori kutsui rakentamiaan soittimia nimellä clavicembalo [tai gravicembalo] col piano e forte eli sananmukaisesti cembalo, jolla voi soittaa hiljaa (ital. piano) ja voimakkaasti (ital. forte) (cembalolla, toisin kuin pianolla, äänenvoimakkuus ei vaihtele sen mukaan, kuinka suurella voimalla koskettimia painetaan). Tämäntyyppisiä soittimia ryhdyttiin pian kutsumaan nimillä fortepiano, josta on tullut suomenkielinen nimitys piano.
        </p>
        <p>
        Pianon rakenne on Christoforin jälkeen merkittävästi kehittynyt. Tärkeitä merkkipaaluja ovat mm. valuraudasta valmistettu tukikehikko, jolloin soittimen kokoa ja kielten jännitystä voitiin huomattavasti lisätä, huovan käyttöönotto nahan sijaan vasaroiden päällysteenä sekä koneiston monet kehitysvaiheet. Kehityksen tuloksena pianon ääni on voimistunut ja tullut kantavammaksi. (Wikipedia)
        </p>

      </div>
    )
  }
}

export default Historia;
