import React from 'react';
import logo from '../logo.jpg';
import {Navbar, NavbarBrand, Nav} from 'react-bootstrap';
import {NavLink} from 'react-router-dom';

class MainNav extends React.Component {
  render() {
    return (
      <nav className="MainNav">

      <Navbar collapseOnSelect expand="lg" variant="dark" className="bg-brown" >

      <NavLink exact to="/">
        <Navbar.Brand>
                <img src={logo} width="30" height="30" className="d-inline-block align-top" alt="P"/>
                {' '}
                JPY
        </Navbar.Brand>
      </NavLink>

        <Navbar.Toggle aria-controls="responsive-navbar-nav" />

        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">

          <NavLink to="/historia"><div class="nav-link">Piano ja sen historia</div></NavLink>
          <NavLink to="/soittaminen"><div class="nav-link">Pianon soittaminen</div></NavLink>
          <NavLink to="/kuuluisuudet"><div class="nav-link">Kuuluisia pianisteja</div></NavLink>
          </Nav>
        </Navbar.Collapse>

      </Navbar>




</nav>

    )
  }
}

export default MainNav;
