import React from "react";
import '../App.css';
import {Form, FormGroup, FormLabel, FormControl, FormText, Button} from 'react-bootstrap';

class Palaute extends React.Component {

  	render(){
  		return(
      <div className="Palaute">
        <h1>Palautelomake</h1>

        <Form className="Form">

          <Form.Group>
            <Form.Label>Lähettäjä</Form.Label>
            <Form.Control type="email" placeholder="Sinun sähköpostiosoitteesi" />
            <Form.Text className="text-muted">
              Ei mene minnekään jakoon.
            </Form.Text>
          </Form.Group>

          <Form.Group>
          <Form.Label>Aihe</Form.Label>
          <Form.Control as="select">
            <option>Risuja</option>
            <option>Ruusuja</option>
            <option>Kysymyksiä</option>
            <option>Vähän kaikkea / Jotain muuta</option>
          </Form.Control>
        </Form.Group>

        <Form.Group>
          <Form.Label>Viesti</Form.Label>
          <Form.Control as="textarea" rows={4} />
        </Form.Group>

          <Form.Group>
            <Form.Check type="checkbox" label="En ole roskapostia" />
            <Form.Text className="text-muted">
              Klikkaa tästä niin tiedämme, ettet ole botti.
            </Form.Text>
          </Form.Group>

          <Button>Lähetä!</Button>

        </Form>

      </div>
    )
  }
}

export default Palaute;
