import React from 'react';
import frontpage from '../frontpage.jpg';
import { Container, Row, Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

class Greet extends React.Component {

	render(){
		return(

			<Container>

				<Row className="text-center">
					<Col>
						<h1>Tervetuloa!</h1>
					</Col>
				</Row>

			  <Row className="text-center">
			    <Col>
						<p>
						Tämä sivusto on tarkoitettu pianoista ja pianonsoitosta kiinnostuneille. Valikkoa tutkimalla löydät tietoa pianon historiasta, soittamaan oppimisesta sekä kuuluisista pianisteista. Alhaalta löydät yhteystietomme.
						</p>
						<img src={frontpage} alt="Chopin esiintymässä" id="upright-piano" />
						<p>
						<i>Chopin esiintymässä.</i>
						</p>
					</Col>
			  </Row>

			</Container>


		);
	}
}

export default Greet;
